'use client'; // <-- nécessaire pour les events interactifs

import './globals.css';
import Link from 'next/link';
import { useState } from 'react';

export const metadata = {
  title: 'Portfolio Hasinirina',
  description: 'Mon portfolio',
};

export default function RootLayout({ children }: { children: React.ReactNode }) {
  const [active, setActive] = useState('#accueil');

  const links = [
    { href: '#accueil', label: 'Accueil' },
    { href: '#about', label: 'À propos' },
    { href: '#projects', label: 'Projets' },
    { href: '#contact', label: 'Contact' },
  ];

  return (
    <html lang="fr">
      <body style={{ margin: 0 }}>
        <header style={{
          position: 'fixed',
          top: 0,
          width: '100%',
          backgroundColor: '#0a1f44',
          color: '#fff',
          zIndex: 1000,
          boxShadow: '0 2px 5px rgba(0,0,0,0.2)'
        }}>
          <nav style={{
            maxWidth: '1200px',
            margin: '0 auto',
            display: 'flex',
            justifyContent: 'space-between',
            alignItems: 'center',
            padding: '15px 20px'
          }}>
            <div style={{ fontWeight: 'bold', fontSize: '22px' }}>HASINIRINA Jean de Dieu</div>
            <div style={{ display: 'flex', gap: '25px' }}>
              {links.map((link) => {
                const isActive = active === link.href;
                return (
                  <Link
                    key={link.href}
                    href={link.href}
                    onClick={() => setActive(link.href)}
                    style={{
                      color: '#fff',
                      textDecoration: 'none',
                      paddingBottom: '5px',
                      borderBottom: isActive ? '3px solid #00bfff' : '3px solid transparent',
                      transition: '0.3s',
                    }}
                    onMouseOver={(e) => (e.currentTarget.style.color = '#00bfff')}
                    onMouseOut={(e) => (e.currentTarget.style.color = isActive ? '#00bfff' : '#fff')}
                  >
                    {link.label}
                  </Link>
                );
              })}
            </div>
          </nav>
        </header>
        <main style={{ paddingTop: '80px', backgroundColor: '#0a1f44', color: '#fff' }}>
          {children}
        </main>
      </body>
    </html>
  );
}

